(function() {
    var windowID = chrome.windows.WINDOW_ID_CURRENT;
    var tabs = {
      getCurrentTab: function () {
        var currentTab = chrome.tabs.query({
          active: true,
          windowId: windowID,
        },
      function (tabs) {
        return tabs[0];
      });
        return currentTab;
      }
    };

    return tabs;
  })();
